module.exports = "### Responsive and Static Images\n\nImage components with customizable, responsive aspect ratio or static sizing.\n\n### Props\n\n```table\nspan: 6\nrows:\n  - Prop: loader\n    Type: node\n    Default: null\n    Notes: Loader UI to display a skeletal state for the image\n  - Prop: src\n    Type: string\n    Default: ''\n    Notes: Path or URL to image\n  - Prop: alt\n    Type: string\n    Default: ''\n    Notes: Alt text for image\n  - Prop: height\n    Type: number\n    Default: 1\n    Notes: Measured in pixels\n  - Prop: width\n    Type: number\n    Default: 1\n    Notes: Measured in pixels\n```\n\n### Image with loader prop\n\n```react\nresponsive: true\n---\n<ResponsiveImage loader={\"Loading...\"} />\n```\n\n### Responsive Image (16:9)\n\n```react\nresponsive: true\n---\n<ResponsiveImage\n    src=\"http://placekitten.com/g/826/465\"\n    alt=\"Test Kitten\"\n    height={465}\n    width={826}\n    loader={null}\n/>\n```\n\n### Responsive Image (1:1)\n\n```react\nresponsive: true\n---\n<ResponsiveImage\n    src=\"http://placekitten.com/g/400/400\"\n    alt=\"Test Kitten\"\n    height={400}\n    width={400}\n    loader={null}\n/>\n```\n\n### Static Image (16:9)\n\n```react\nresponsive: true\n---\n<StaticImage\n    src=\"http://placekitten.com/g/826/465\"\n    alt=\"Test Kitten\"\n    height={465}\n    width={826}\n    loader={null}\n/>\n```\n\n### Static Image (1:1)\n\n```react\nresponsive: true\n---\n<StaticImage\n    src=\"http://placekitten.com/g/400/400\"\n    alt=\"Test Kitten\"\n    height={400}\n    width={400}\n    loader={null}\n/>\n```\n"

